
.content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    place-content: space-evenly;
}

.cont {
    display: grid;
    place-content: center;
}


.content img{
    height: 15vh;
    width: 15vh;
    margin: auto;
}

